.empty-page {
  text-align: center;
  max-width: 300px;
  color: var(--text-color);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  margin-top: 200px;
  margin-bottom: 60px;
  font-size: 14px;
  display: flex;
}

.favorites-img-tablet, .favorites-img-desktop {
  display: none;
}

@media screen and (width >= 768px) {
  .empty-page {
    max-width: 400px;
    margin-top: 20px;
    font-size: 16px;
  }

  .favorites-img-tablet {
    display: block;
  }
}

@media screen and (width >= 1280px) {
  .empty-page {
    max-width: 524px;
  }

  .favorites-img-desktop {
    display: block;
  }

  .favorites-img-tablet {
    display: none;
  }
}
